
import {
  mapState,
  mapActions
} from 'vuex'
import api from '../api'
import CardList from './card-list'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

const list_type = ['small', 'medium', 'large'] //small--小图, medium--横向  large --并排
export default {
  name: 'list',
  data: function() {
    return {
     goods_data:[],
     current_page: 0,
     last_page: 0,
     total: 0,
     from:0,
     to:0,
     per_page:0,
      demo4Value: {
        pullupStatus: 'default'
      },
      loading: false,
      type: 'medium',
      search_input: '',
      pullupConfig: {
        content: '上拉加载更多',
        downContent: '松开进行加载',
        upContent: '上拉加载更多',
        loadingContent: '加载中...'
      }
    }
  },
  components: {
    Flexbox,
    FlexboxItem,
    Search,
    CardList,
    Scroller,
    Spinner
  },
  watch:{
    '$route':function(to,from){
      console.log("切换");
    }
  },
  created: function() {
    this.handler_query();
  },
  computed: mapState({
    goods_data: state => state.goods.goods_list.data,
    current_page: state => state.goods.goods_list.current_page,
    last_page: state => state.goods.goods_list.last_page,
    total: state => state.goods.goods_list.total,
    from: state => state.goods.goods_list.from,
    to: state => state.goods.goods_list.to,
    per_page: state => state.goods.goods_list.per_page
  }),
  methods: {
    ...mapActions(['GETGOODSLIST']),
    toggleType: function() {
      //console.log("切换");
      this.type = this.type == 'medium' ? 'large' : 'medium';
    },
    load: function() {
      console.log("上拉加载");
      this.loading=true;
    },
    submit_search:function(){
      console.log("搜索测试");
      var self=this;
      this.handler_query({search:true});
    },
    handler_query:function(params){
       var self=this;
       var query=this.$route.query;
        console.log(query);
        if(!!params&&params.search){
          query={search:self.search_input};
        }
        console.log(query);
        query.relations=["images"];
        if(query["search"]){
           api.get_search_result(query).then(res=>{
               console.log(res);
                self.$store.state.goods.goods_list=res.data;
                self.goods_data=res.data.data;
                self.current_page=res.data.current_page;
                self.last_page=res.data.last_page;
                self.total=res.data.total;
                self.from=res.data.from;
                self.to=res.data.to;
                self.per_page=res.data.per_page;
               console.log("********************");
           })
        }else{
          api.getGoodsData({relations: ["images","image_attach"], parameters:query, per_page: 10 }).then(res=>{
                console.log(res);
                self.$store.state.goods.goods_list=res.data;
                self.goods_data=res.data.data;
                self.current_page=res.data.current_page;
                self.last_page=res.data.last_page;
                self.total=res.data.total;
                self.from=res.data.from;
                self.to=res.data.to;
                self.per_page=res.data.per_page;
               console.log("**************222******");
           })
           // { relations: ["image_attach", "images"], parameters:[{goods_id:39}], per_page: 10 }
          //this.GETGOODSLIST({ relations: ["image_attach", "images"], parameters:query, per_page: 10 });
        }
    }
  }

}



